.try-again-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.try-again-container > h1{
    text-decoration: underline;
    margin: 0;
    font-size: 36px;
    margin-bottom: 20px;
}

.result-container > p{
    font-size: 20px;
    margin: 7px;
}

.end-btns{
    position: relative;
    margin: 12px;
    border: none;
    border-radius: 4px;
    min-width: 64px;
    padding: 2px 16px;
    color: aliceblue;
    font-size: 18px;
    font-weight: 500;
    line-height: 36px;
    cursor: pointer;
    transition: background-color 0.5s;
}

.start-again-btn{
    background-color: #3e4248;
}

.start-again-btn:hover,
.start-again-btn:focus{
    background-color:#808080
}

.share-btn{
    background: #3b5998;
}

.share-btn:hover,
.share-btn:focus{
    background: #5d7dc4;
}

.tweet-btn{
    background: #1da1f2;
}

.tweet-btn:hover,
.tweet-btn:focus{
    background-color: #8faef3;
}